@import '../../styles/_util';

.detailsInfo {
  width: 100%;

  margin: 1rem 0;

  p {
    display: flex;
    justify-content: space-between;

    width: 100%;

    & span:first-of-type {
      padding-right: 1.5rem; } } }
////////////////////////////////////////////
.card {
  position: relative;

  display: flex;
  flex-direction: column;

  width: 300px;
  max-width: 95%;

  margin: 2rem 0.25rem;
  padding: 1.5rem;

  border: solid 1px black;

  border-radius: 1rem;

  .title {
    margin-bottom: 0.5rem; } }

.figure {
  width: 100%;
  max-height: 50%;

  margin-bottom: auto;

  img {
    max-height: 100%;
    width: 100%;

    object-fit: cover; } }

.price {
  margin: 1rem 0;

  color: darkgreen;
  font-weight: bold;
  font-size: large; }

.details {
  cursor: pointer;
  position: relative;

  margin: 1rem 0;

  border: dotted 1px black;
  border-radius: 0.5rem;

  .title {
    margin: 0;

    padding: 0.5rem; }

  &:hover {
    background-color: $primary;

    .detailsContainer {
      display: flex; } }

  .detailsContainer {
    position: absolute;
    bottom: calc(100% + 1rem);

    margin: auto;

    display: none;
    justify-content: space-between;
    flex-wrap: wrap;

    padding: 0.5rem 1rem;

    background-color: white;
    border: solid 1px black;
    border-radius: 1rem; } }

.nutrition {
  @extend .detailsInfo; }

.classification {
  @extend .detailsInfo; }

.button {
  width: 100%;

  padding: 0.75rem 1rem;

  color: white;
  background-color: $primary;

  border: solid 1px orange;
  border-radius: 1rem;

  &:hover {
    cursor: pointer;
    background-color: orange; }

  &:active {
    background-color: $primary; } }
