@import '../../styles/_util';

.shoppingCartMenu {
  width: 1000px;
  max-width: 90%;

  margin: 3rem auto;

  background-color: orange;

  border-collapse: collapse; }

.tableContainer {}

.itemContainer {
  width: 100%;

  border-collapse: collapse;
  border: solid 1px black;

  colgroup {
    col {
      width: fit-content;

      &:first-child {
        width: auto; } } }

  thead {
    th {
      text-align: start; } }

  tbody {
    tr {
      &:nth-of-type(2n) {
        background-color: $secondary; }

      &:nth-of-type(2n + 1) {
        background-color: $alt; }

      td {
        width: fit-content;

        &:last-of-type {
          text-align: end; } } } }

  td, th {
    padding: 0.5rem 1rem;

    border: solid 1px black; } }

.finish {
  display: flex;
  justify-content: space-between;

  padding: 1rem 0; }

.finishText {
  display: flex;
  align-items: center;

  padding-left: 1rem;

  font-weight: bold;
  font-size: large;

  .finishTextInside {
    margin-left: 1rem;

    color: darkgreen; } }

.finishButtonContainer {}

.finishButton {
  margin: 0.5rem;
  padding: 0.75rem 1.5rem;

  background-color: white;
  color: orange;
  font-weight: bold;
  font-size: medium;

  border: solid 1px $primary;
  border-radius: 1rem;

  &:hover {
    cursor: pointer;

    color: white;
    background-color: $primary; }

  &:active {
    background-color: orange; } }

.buyMenuContainer {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;

  display: flex;
  justify-content: center;
  align-items: center;

  & > div {
    height: 100vh;
    width: 100vw;

    background-color: #0008; } }

.buyMenu {
  position: fixed;

  padding: 2rem;

  background-color: orange;
  border: solid 1px black;
  border-radius: 1rem; }

.receipt {
  width: 100%; }

.receiptItem {
  display: flex;
  justify-content: space-between;

  padding: 0.5rem 0;

  div {
    p {
      display: inline-block;
      padding-left: 1rem; } } }

.confirmation {
  display: flex;
  justify-content: center;

  margin-top: 1rem;

  button {
    cursor: pointer;
    flex-grow: 1;

    padding: 0.75rem 1.5rem;

    background-color: white;
    color: orange;
    font-size: large;
    font-weight: bold;

    border: solid 1px $primary;
    border-radius: 0.75rem;

    &:hover {
      color: white;
      background-color: $secondary; }

    &:active {
      background-color: $alt; }

    &:first-child {
      margin-right: 1rem; }

    &:last-child {} } }

.money {
  color: darkgreen; }
